<template>
    <!--订单详情内容-->
    <div class="_shopOrder d-flex justify-content-center align-items-start ">
        <div id="_Order-box" class="_Order-box card card-body px-0 py-1 mx-0 ">
            <!--设置标题栏的后退功能-->
            <div class="" style="position:absolute;left:0;top:0;">
                <div class="txt-subtitle txt-subtitle-space  px-2 px-sm-3 " style="font-weight:bold"
                     @click="$router.go(-1)">
                    <i class="iconfont icon-left" style="font-size:22px;color:#1f1f1f"></i>
                    <span>返回</span>
                </div>
            </div>
            <!--订单标题栏-->
            <div class="goodsTitle d-flex justify-content-center pt-2">
                <span class="txt-subtitle" style="font-weight:bold;color:#434B43">{{statusString}}订单</span>
            </div>
            <!--订单商品栏-->
            <div class="goodsContent">
                <goods-item :allList="detail.orderDetails.data" :path="$route.fullPath">
                    <template v-slot:numSlot="subdata">
                        <span class="_myNum txt-body px-5  ">x{{subdata.item.num}}</span>
                    </template>
                    <template v-slot:bottom="subdata">
                        <div class="d-flex justify-content-end align-items-end px-3 py-1 text-muted txt-subbody">
                            <div class="px-1">
                                总价
                                <small>￥</small>
                                <span class="txt-subbody">{{subdata.item.goods.price+2}}</span>
                                <small>.00,</small>
                            </div>
                            <div class="px-1">
                                优惠
                                <small>￥</small>
                                <span class="txt-subbody">2</span>
                                <small>.00,</small>
                            </div>
                            <div class="px-1 text-black " style="font-weight:bold">
                                实付款
                                <small>￥</small>
                                <span class="txt-subbody">{{subdata.item.goods.price}}</span>
                                <small>.00</small>
                            </div>
                        </div>
                        <div class="d-flex justify-content-end align-items-center pb-1 px-3">
                            <div class="txt-subbody btn rounded rounded-pill px-2 py-1 mx-2"
                                 @click="onPayOrder(subdata.item.id)"
                                 style="color:#5D6A68; border:2px solid #BAF3EA" v-if="detail.status===1">支付订单
                            </div>
                            <div class="txt-subbody btn rounded rounded-pill px-2 py-1 mx-2"
                                 @click="goTo('/connecthost')"
                                 style="color:#5D6A68; border:2px solid #BAF3EA" v-if="detail.status===2">联系卖家
                            </div>
                            <div class="txt-subbody btn rounded rounded-pill px-2 py-1 mx-2"
                                 @click="goTo('/logistics',{'id':subdata.item.id})"
                                 style="color:#5D6A68; border:2px solid #BAF3EA" v-if="detail.status===3">查看物流
                            </div>
                            <div class="txt-subbody btn rounded rounded-pill px-2 py-1 mx-2"
                                 @click="onConfirmAccept(subdata.item.id)"
                                 style="color:#5D6A68; border:2px solid #BAF3EA" v-if="detail.status===3">确认收货
                            </div>
                            <div class="txt-subbody btn rounded rounded-pill px-2 py-1 mx-2"
                                 @click="goTo('/connecthost',{'id':subdata.item.id})"
                                 style="color:#5D6A68; border:2px solid #BAF3EA" v-if="detail.status===4">进行评价
                            </div>
                            <div class="txt-subbody btn rounded rounded-pill px-2 py-1"
                                 @click="onAddCart(subdata.item.goods_id,subdata.item.num)"
                                 style="color:#5D6A68; border:2px solid #BAF3EA">加入购物车
                            </div>
                        </div>
                    </template>
                </goods-item>
            </div>
            <!--订单详情栏-->
            <div class="payContent d-flex flex-column justify-content-around pt-2 pt-sm-3">
                <div class="align-self-start px-5 txt-subtitle py-2" style="font-weight:bold">订单信息</div>
                <div class="row d-flex align-items-center py-1" v-if="addressInfo.name.length > 0">
                    <div class="col-4 col-sm-3 col-md-2 d-flex justify-content-start">
                        <span class="px-3">收货信息</span>
                    </div>
                    <div class=" col-8 col-sm-9 col-md-10 d-flex flex-column justify-content-start ">
                        <div class="w-100 px-2  text-truncate d-flex justify-content-start ">
                            <span class=" text-truncate ">{{addressInfo.name}} {{addressInfo.tel}} {{addressInfo.province}}
                            {{addressInfo.city}} {{addressInfo.county}} </span>
                        </div>
                        <div class="w-100 px-2  text-truncate d-flex justify-content-start ">
                            <span class=" text-truncate ">{{addressInfo.addressDetail}}</span>
                        </div>
                    </div>
                </div>
                <div class="row d-flex align-items-center py-1" v-else>
                    <div class="col-4 col-sm-3 col-md-2 d-flex justify-content-start">
                        <span class="px-3">收货信息</span>
                    </div>
                    <div class="col-8 col-sm-9 col-md-10 d-flex justify-content-start ">
                        <span class=" px-2  text-truncate">购买商品的地址{{detail.address_id}}</span>
                    </div>
                </div>
                <div class="row d-flex  align-items-center py-1">
                    <div class="col-4 col-sm-3 col-md-2 d-flex justify-content-start">
                        <span class="px-3">订单编号</span>
                    </div>
                    <div class="col-8 col-sm-9 col-md-10 d-flex justify-content-start  ">
                        <span class=" px-2  text-truncate">{{detail.order_no}}</span>
                    </div>
                </div>
                <div class="row d-flex align-items-center py-1">
                    <div class="col-4 col-sm-3 col-md-2 d-flex justify-content-start">
                        <span class="px-2 px-sm-3">支付宝交易号</span>
                    </div>
                    <div class="col-8 col-sm-9 col-md-10 d-flex justify-content-start ">
                        <span class=" px-2 text-truncate">{{detail.trade_no}}</span>
                    </div>
                </div>
                <div class="row d-flex align-items-center py-1">
                    <div class="col-4 col-sm-3 col-md-2 d-flex justify-content-start">
                        <span class="px-3">创建时间</span>
                    </div>
                    <div class="col-8 col-sm-9 col-md-10 d-flex justify-content-start ">
                        <span class=" px-2  text-truncate">{{detail.created_at}}</span>
                    </div>
                </div>
                <div class="row d-flex align-items-center py-1">
                    <div class="col-4 col-sm-3 col-md-2 d-flex justify-content-start">
                        <span class="px-3">付款时间</span>
                    </div>
                    <div class="col-8 col-sm-9 col-md-10 d-flex justify-content-start ">
                        <span class=" px-2  text-truncate">{{detail.pay_time}}</span>
                    </div>
                </div>
                <div class="row d-flex align-items-start py-1">
                    <div class="col-4 col-sm-3 col-md-2 d-flex justify-content-start">
                        <span class="px-3">发货时间</span>
                    </div>
                    <div class="col-8 col-sm-9 col-md-10 d-flex justify-content-start  ">
                        <span class="  px-2 text-truncate">{{detail.updated_at}}</span>
                    </div>
                </div>
                <div class="row d-flex align-items-center py-1">
                    <div class="col-4 col-sm-3 col-md-2 d-flex justify-content-start">
                        <span class="px-3">成交时间</span>
                    </div>
                    <div class="col-8 col-sm-9 col-md-10 d-flex justify-content-start ">
                        <span class=" px-2  text-truncate">{{detail.success_at}}</span>
                    </div>
                </div>
                <div class="row d-flex  align-items-center py-2 mt-2" style="border-top:1px solid #C7FAF9">
                    <div class="col-6 d-flex justify-content-center align-items-center"
                         @click="$router.push({path:'/connecthost'})">
                        <i class="iconfont icon-connecthost" style="font-size:22px;color:#49BEF2"></i>
                        <span class="px-1">联系卖家</span>
                    </div>
                    <div class="col-6  d-flex justify-content-center align-items-center"
                         @click="$router.push({path:'/usephone'})">
                        <i class="iconfont icon-dianhua1" style="font-size:22px;color:#49BEF2"></i>
                        <span class="px-1">拨打电话</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import $ from 'jquery';
    import {ElLoading, ElMessage} from 'element-plus';
    import {addCart} from "network/cart";
    import {getAddressDetail} from "network/address";
    import {getOrderDetail, confirmOrder, payOrder, payOrderStatus} from 'network/order';
    import GoodsItem from 'components/content/goods/GoodsItem';

    import {useRoute, useRouter} from 'vue-router';
    import {useStore} from 'vuex';
    import {ref, toRefs, reactive, onMounted, computed} from 'vue';

    export default {
        name: 'OrderDetail',
        components: {
            GoodsItem,
        },
        setup() {
            const route = useRoute();
            const router = useRouter();
            const store = useStore();

            const state = reactive({
                //orderNo: '',      // 订单ID

                detail: {           // 数据结构必须清晰，引用的数据才能查找的到
                    orderDetails: {
                        data: []
                    },
                    status: 0,
                    address_id: '',     //   收货信息， 地址
                    order_no: '',     //  订单编号
                    trade_no: '',     //  支付宝交易号
                    created_at: '',     //   创建时间
                    pay_time: '',       //   付款时间
                    updated_at: '',     //   发货时间
                    success_at: '',     //   成交时间
                },
                addressInfo: {               // 地址信息
                    name: '',
                    tel: '',
                    province: '',
                    city: '',
                    county: '',
                    addressDetail: '',
                },
                showPay: false,
                aliyun: '',
                wechat: ''
            });
            onMounted(() => {
                let loadingInstance = ElLoading.service({fullscreen: false, text: '加载页面中。。。', target: '._Order-box'});
                initOrder();

                if (loadingInstance !== null) {
                    loadingInstance.close();                // 页面加载中...
                    loadingInstance = null;
                }
            });

            // 初始化数据
            const initOrder = () => {
                const {id} = route.query;
                let loadingInstance = ElLoading.service({fullscreen: false, text: '加载页面中。。。', target: '._Order-box'});
                getOrderDetail(id).then(res => {
                    state.detail = res;
                    Object.keys(state.detail).forEach(function (key) {
                        if (state.detail[key] === null)
                            state.detail[key] = "暂无数据";
                    });
                    if (state.detail.success_at === undefined) {
                        state.detail.success_at = "暂无数据"
                    }
                    console.log('orderdetail-data', res);
                    initAddress(state.detail.address_id);
                }).catch(err => {
                    console.log('orderdetail-err', err);
                    ElMessage.error("获取订单详情失败");
                });
                if (loadingInstance !== null) {
                    loadingInstance.close();                // 页面加载中...
                    loadingInstance = null;
                }
            };

            const initAddress = (address_id) => {
                console.log('获取地址的id是', address_id);
                getAddressDetail(address_id).then(res => {     // 获取修改信息
                    const addressDetail = res;
                    console.log('address-data:', res);
                    let _areaCode = '';

                    let c = state.areaList.county_list;
                    // 遍历对象数据，c_item可遍历出对象的方法和属性名称，c[c_item]即为对应的属性值和方法
                    for (var c_item in c) {
                        if (typeof (c[c_item]) != 'function') {
                            if (c[c_item] == addressDetail.county) {
                                // console.log('c_item:',c_item);
                                _areaCode = c_item;
                            }
                        }
                    }
                    state.addressInfo.name = addressDetail.name;
                    state.addressInfo.tel = addressDetail.phone;
                    state.addressInfo.province = addressDetail.province;
                    state.addressInfo.city = addressDetail.city;
                    state.addressInfo.county = addressDetail.county;
                    state.addressInfo.addressDetail = addressDetail.address;


                    console.log('addressinfo:', state.addressInfo);
                    console.log('数据是', state.addressInfo.province, state.addressInfo.city, state.addressInfo.county);
                }).catch(err => {
                    ElMessage.error('原地址信息已经被删除，暂时无法精确查询地址！')
                });
            };

            // 订单状态

            /* 订单1是新订单， 可以进行支付 */
            /* 订单2是支付成功，  (等待发货)，可以联系卖家发货*/
            /* 订单3是已经发货， 可以进行确认收货，和查看物流*/
            /* 订单4是已经收货， 可以进行评价*/
            /* 订单5是已经过期， (不可以进行订单操作),可以加入购物车*/
            const statusString = computed(() => {

                //1. 新单， 2.支付  3.发货   4.收货    5.过期
                let status = ['', '未下单', '已支付', '已发货', '已收货', '已过期'];
                return status[state.detail.status];
            });


            // 添加购物车
            const onAddCart = (id, num) => {
                addCart({goods_id: id, num: num}).then(res => {
                    if (res.status === 201 || res.status === 204) {               // 状态码是number类型
                        ElMessage.success('添加成功');
                        store.dispatch('updateCart');                   // 分发数据，更新cartCount的值
                    }
                }).catch(err => {
                    console.log('orderdetail-addcart-err',err);
                    ElMessage.success('添加失败');
                });
            };


            // 跳转其它页面
            const goTo = (newPath, newQuery) => {
                router.push({path: newPath, query: newQuery || {}});
            };


            // 支付定单
            const onPayOrder = () => {
                ElMessage.warning('暂时不支持在当前页面进行支付定单')
            };

            // 确认收货
            const onConfirmAccept = (orderId) => {
                console.log('当前在确认收货', orderId);
                confirmOrder(orderId).then(res => {
                    if (res.status === 204) {
                        ElMessage.success('确认收货成功！')
                    } else {
                        ElMessage.error('确认收货失败！')
                    }
                }).catch(err => {
                    console.log('confirmAccept-err', err);
                    ElMessage.error('确认收货失败！请重试');
                })
            };
            //  --------- 支付定单, 已经放弃开发-------
            /* // 计算总价
             const total = computed(() => {
                 let sum = 0;
                 for (var i in state.detail.orderDetails.data) {
                     var item = state.detail.orderDetails.data[i];
                     sum += parseInt(item.num) * parseFloat(item.goods.price);
                 }
                 // computed里面无法再使用数组或对象的内置方法
                 // state.cartList.forEach(item=>{
                 //    sum += parseInt(item.num) * parseFloat(item.goods.price);
                 // });
                 return sum;
             });
             // 显示支付
             const showPayFn = () => {           // v-model进行过双向绑定，即可响应式改变数据。
                /!* state.showPay = true;

                 // 进行订单支付
                 payOrder(state.orderNo, {type: 'aliyun'}).then(res => {
                     state.aliyun = 'https://api.shop.eduwork.cn' + res.qr_code_url;
                 });
                 payOrder(state.orderNo, {type: 'wechat'}).then(res => {
                     state.wechat = 'https://api.shop.eduwork.cn' + res.qr_code_url;
                 });
                 // 查看支付状态
                 var maxTimeout = 10;
                 var turns = 0;
                 const timer = setInterval(() => {
                     payOrderStatus(state.orderNo).then(res => {
                         if (res == '2') {
                             clearInterval(timer);
                             // router.push({path:'/order',query:{status:2}});
                             router.push({path: '/order', query: {id: state.orderNo}});
                         } else {
                             console.log('等待支付中');
                         }
                         turns++;
                         if (turns >= maxTimeout) {
                             clearInterval(timer);
                             console.log('支付超时！');
                             alert('支付超时！请重新提交订单！');
                         }
                     })
                 }, 2000)*!/
             };*/


            return {...toRefs(state), statusString, onAddCart, goTo, onPayOrder, onConfirmAccept}
        }
    }

</script>
<style scoped lang="scss">
    ._shopOrder {
        min-height: 600px;

        #_Order-box {
            max-width: 1200px;
            min-height: 580px;
        }

        ._tab-order li {
            width: 20%;
        }

        ._tab-order li span {
            border-bottom: 3px solid white;
        }

        ._tab-order li.active span {
            border-bottom: 3px solid red;
        }

        .goodsContent, {
            position: relative;
            width: 100%;
            min-height: 100px;
            left: 0;
            top: 0;
            right: 0;
            margin: 0 auto;

            ._shopItemBox {
                border: 2px solid #BAF3EA;
                border-radius: 10px;
            }
        }
    }
</style>